.cart-page {
  padding-top: 40px;
  margin-bottom: 80px;
  width: 300px;

  @media (min-width: 768px) {
    width: 600px;
  }

  @media (min-width: 1440px) {
    width: 1160px;
  }

  &__back-link {
    margin-bottom: 16px;
  }

  &__title {
    margin-bottom: 24px;
    color: #313237;
    font-size: 32px;
    font-weight: 700;
    letter-spacing: -0.32px;
  }

  &__content {
    display: flex;
    gap: 16px;
    color: #313237;
    font-size: 20px;
    font-weight: 600;
    flex-direction: column-reverse;
    transition: all 300ms;

    @media (min-width: 1440px) {
      flex-direction: row;
    }
  }

  &__products {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__checkout {
    box-sizing: border-box;
    width: 100%;
    height: 206px;
    border: 1px solid #313237;
    padding: 24px;
    transition: all 300ms;
  }

  &__total {
    text-align: center;
    color: #313237;
    font-size: 32px;
    font-weight: 700;
    letter-spacing: -0.32px;
  }

  &__total-items {
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid #313237;
    color: #313237;
    text-align: center;
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
  }

  &__button {
    width: 100%;
    height: 48px;
    background-color: #313237;
    color: #fff;
    text-align: center;
    line-height: 48px;
    font-size: 14px;
    font-weight: 600;
    transition: all 300ms;
    cursor: pointer;

    &:hover {
      box-shadow: 0 10px 20px 0 rgba(23, 32, 49, 0.26);
      background-color: #159f75;
    }
  }

  &__message {
    color: #313237;
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    transform: translateY(50px);
  }
}
